<template>
  <div>
    <el-dialog :visible.sync="visible" :before-close="beforeClose" :append-to-body="true" title="新增人员" width="800px">
      <div class="p15">
        <el-form :show-message=true :inline-message=true ref="form" :model="form" :rules="rules" class="form-text f13 " label-width="108px" size="mini">
          <el-row>
            <el-col :span="24">
              <el-form-item label="审批人角色">
                <el-radio-group v-model="form.role">
                  <el-radio :label="1">主管-指定级别</el-radio>
                  <el-radio :label="2">指定人员</el-radio>
                  <el-radio :label="3">指定角色(一组人员)</el-radio>
                  <el-radio :label="4">发起人</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt10" v-if="form.role === 1">
            <el-col :span="24">
              <el-form-item label="">
                  指定某一级主管：&nbsp发起人的&nbsp
                    <el-select v-model="form.roleExtra" class="prefix_select">
                      <el-option label='直接主管' value='1'></el-option>
                      <el-option label='第2级主管' value='2'></el-option>
                      <el-option label='第3级主管' value='3'></el-option>
                    </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt10"  v-if="form.role === 2 || form.role === 3">
            <el-col :span="24">
              <el-form-item label="">
                选择指定角色
                <el-select v-model="form.roleExtra" class="prefix_select">
                  <el-option label='直接主管' value='1'></el-option>
                  <el-option label='第2级主管' value='2'></el-option>
                  <el-option label='第3级主管' value='3'></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt10">
            <el-col :span="16">
              <el-form-item label="审批方式">
                <el-radio-group v-model="form.method">
                  <el-radio :label="1">审批</el-radio>
                  <el-radio :label="2">确认</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt10">
            <el-col :span="24">
              <el-form-item label="审批要求">
                <el-radio-group v-model="form.require">
                  <el-radio :label="1">会签(须所有审批人同意)</el-radio>
                  <el-radio :label="2">或签(角色中一人同意或拒绝即可)</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="mt10">
            <el-col :span="7">
              <el-col :span="18">
                <el-form-item label="处理时长" prop="timeLimit">
                  <el-input
                    v-model='form.timeLimit'>
                  </el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6" style="line-height: 30px">
                <span>&nbsp天</span>
              </el-col>
            </el-col>
          </el-row>
          <el-row class="mt10">
            <el-col :span="16">
              <el-form-item label="" prop="extraCheck">
                <el-checkbox v-model="form.extraCheck">若该审批人空缺，由其在组织部门中的上级主管代审批</el-checkbox>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer mt10" style="text-align: center">
        <el-button type="primary" @click="closeDialog('save')">保 存</el-button>
        <el-button @click="closeDialog('close')">取 消</el-button>
      </div>
    </el-dialog>
    <at-modal v-model="showModal"
              @on-ok="closeModal">
      asdasasds
    </at-modal>
  </div>
</template>
<script>
  import {AtModal} from '@/components/modal'
  export default {
    name: 'Dialog',
    components: {
      AtModal
    },
    props: ['dialogFormVisible'],
    computed: {
      visible: {
        get: function () {
          return this.dialogFormVisible
        },
        set: function () {
        }
      }
    },
    data() {
      return {
        showModal: false,
        form: {
          extraCheck: false,
          role: 1,
          method: 1,
          roleExtra: '',
          require: 1,
          timeLimit: 5
        },
        rules: {
          name: [
            { required: true },
          ],
          phone: [
            { required: true },
          ],
          pwd: [
            { required: true },
          ]
        }
      }
    },
    methods: {
      closeDialog(method) {
        this.$emit('closeDialog', this.form, method)
        if (method !== 'close') {
          this.form = {
            date: '',
            gender: 1,
            status: 1,
            positionSelect: '',
            name: '',
            id: null,
            pwd: '',
            department: '',
            email: '',
            note: '',
            born: '',
            callHeader: '',
            callBody: '',
            weChat: ''
          }
          this.$refs['form'].resetFields()
        }
      },
      modalShow() {
        this.showModal = true
      },
      closeModal(value) {
        this.form.department = value
      },
      beforeClose(done) {
        done()
        this.$emit('closeDialog', {}, 'close')
      }
    }
  }
</script>
<style lang="scss">
  .prefix_select{
    .el-input {
      .el-input__inner {
        top: 0
      }
    }
  }
</style>


